import { Component, OnInit,Inject} from '@angular/core';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material';
import { DialogData } from '../patient/patient.component';
import {Observable} from 'rxjs';
import { formatDate } from '@angular/common';
import {AppointmentService} from  '../services/appointment.service';
import { datepickerAnimation } from 'ngx-bootstrap/datepicker/datepicker-animations';

//import { DatePipe } from '@angular/common';
@Component({
  selector: 'app-edit-appointment',
  templateUrl: './edit-appointment.component.html',
  styleUrls: ['./edit-appointment.component.scss']
})
export class EditAppointmentComponent implements OnInit {
  hpls:any={};
  id:any={};
  doctors:any={};
  event:any={};
  hospid:any={};  
  r:any=[];
  roledetails:any=[];
  constructor(private appointmentservice:AppointmentService,public dialog:MatDialog,
    public dialogRef: MatDialogRef<EditAppointmentComponent>,
    @Inject(MAT_DIALOG_DATA) public data:Observable<any>) {
   
      
    }
    
   onNoClick(): void {
    this.dialogRef.close();
  }
      
  ngOnInit() {
      this.r=JSON.parse(localStorage.getItem("currentUser"));
       this.roledetails.role=this.r['role'];   
      //alert(JSON.stringify(this.r['role']));
      this.appointmentservice.getHospitals().subscribe(res => {
      this.hpls=res;
    });

   this.id={
   "id":this.data['hospid']
  };
 this.appointmentservice.getDoctors(this.id).subscribe(res => {
 this.doctors=res;})

     
  }
  public applySelect(event) {
    this.id={
        "id":event.value
    };
   this.appointmentservice.getDoctors(this.id).subscribe(res => {
   this.doctors=res;})
  //alert(JSON.stringify(this.data));
}
radioChange($event){
   this.data['gender']=$event.value;

}

changeDate($event){
  const format='yyyy-MM-dd';
  const locale='en-us';
  const d=formatDate($event,format,locale);
  this.data['date']=d;
  
}
}

