import { Component, OnInit } from '@angular/core';

declare const $: any;

declare interface PatientInfo {
    path: string;
    title: string;
    icon: string;
    class: string;
}
declare interface DoctorInfo{
    path: string;
    title: string;
    icon: string;
    class: string;

}
export const ROUTES1: PatientInfo[] = [
    { path: '/dashboard', title: 'Dashboard',  icon: 'dashboard', class: '' },
    {path:'/patient',title:'Patient', icon:'person',class:''},
    { path: '/create-appointment', title: 'Create Appointment',  icon:'library_books', class: '' },
    {path:'/appointment-details',title:'Appointment Details',icon:'library_books',class:''}
    //{ path: '/typography', title: 'Typography',  icon:'library_books', class: '' },
    //{ path: '/icons', title: 'Icons',  icon:'bubble_chart', class: '' },
    //{ path: '/maps', title: 'Maps',  icon:'location_on', class: '' },
  //  { path: '/member', title: 'Members',  icon:'notifications', class: '' },
    //{ path: '/upgrade', title: 'Upgrade to PRO',  icon:'unarchive', class: 'active-pro' },
];

export const ROUTES2: DoctorInfo[] = [
    { path: '/dashboard', title: 'Dashboard',  icon: 'dashboard', class: '' },
   { path: '/doctor', title: 'Patient Details',  icon:'content_paste', class: '' },
        
];
@Component({
  selector: 'app-sidebar',
  templateUrl: './sidebar.component.html',
  styleUrls: ['./sidebar.component.css']
})
export class SidebarComponent implements OnInit {
  menuItems: any[];
  data:any=[];
  constructor() { }

  ngOnInit() {
    this.data=JSON.parse(localStorage.getItem("currentUser"));
     if(this.data['role']=='patient'){
        this.menuItems = ROUTES1.filter(menuItem => menuItem);
     }else
     this.menuItems=ROUTES2.filter(menuItem => menuItem);
  }
  isMobileMenu() {
      if ($(window).width() > 991) {
          return false;
      }
      return true;
  };
}
