import { Component, OnInit,Inject} from '@angular/core';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material';
import { MatTableDataSource } from '@angular/material';
import { empty } from 'rxjs';
import {DatePipe} from '@angular/common';
import {Observable} from 'rxjs';

import * as pdfMake from 'pdfmake/build/pdfmake';
import * as pdfFonts from 'pdfmake/build/vfs_fonts';
pdfMake.vfs = pdfFonts.pdfMake.vfs;
export interface Element {
  desc: string;
  cost:number;
}


const ELEMENT_DATA: Element[]=[{desc:'',cost:null}
 
];



@Component({
  selector: 'app-printdetails',
  templateUrl: './printdetails.component.html',
  styleUrls: ['./printdetails.component.scss']
})
export class PrintdetailsComponent implements OnInit {
  displayedColumns= ['desc', 'cost','delete'];
  dataSource = new MatTableDataSource();

  index:number;
  cost:number;
  patdtls:any={};
  patdtlsdate:any={};
  patdtlsdesc:any={};
  d:any=[];
  t:any=[];
  PrintDetails : any ={
    date:'',
    patname:'',
    docname:'',
    desc:'',
    cost:''
  };
  constructor(private datePipe: DatePipe,public dialogRef: MatDialogRef<PrintdetailsComponent>,
    @Inject(MAT_DIALOG_DATA) public data:Observable<any>) {}
  
    onNoClick(): void {
      this.dialogRef.close();
    }
  ngOnInit() {
    var date = this.datePipe.transform(new Date(),"dd-MM-yyyy");
    var time=this.datePipe.transform(new Date(),"shortTime");
    this.d=date;
    this.t=time;
   // this.dataSource.data
  }
 
  applyFilter(filterValue: string) {
    filterValue = filterValue.trim(); // Remove whitespace
    filterValue = filterValue.toLowerCase(); // MatTableDataSource defaults to lowercase matches
    this.dataSource.filter = filterValue;
  }
  
  addElement() {
    ELEMENT_DATA.push({desc:'',cost:null})
    this.dataSource = new MatTableDataSource(ELEMENT_DATA);
  }
rowIndex(i){
this.index=i;
this.dataSource.data.splice(this.index,1);
this.dataSource._updateChangeSubscription();

}
 
removeSelectedRows(element) {
  
  this.dataSource.data.splice(this.index,1);
  this.dataSource._updateChangeSubscription();
}


/** Gets the total cost of all transactions. */
 getTotalCost(){

return ELEMENT_DATA.map(t => t.cost).reduce((acc, value) => +acc + +value, 0);
 }
 submit(){
  // alert(JSON.stringify(this.data));
   this.patdtls=JSON.stringify(this.data);
   alert(this.patdtls);

   this.patdtlsdate=JSON.stringify(this.d);
   alert(this.patdtlsdate);
   
   this.patdtlsdesc=JSON.stringify(this.dataSource.data);

   alert(this.patdtlsdesc);
   const docDefinition = {
    content: [
      {
        text:'Invoice Details\n\n\n\n',
        style: 'header',
        alignment:'center',
        color:'blue'
     },
     {
       text:this.t,
       alignment:'right'
     },
     {
      text:this.d,
      alignment:'right'
     },
     {
			style: 'tableExample',
			table: {
				body: [
					['Description', 'Cost'],
					['desc', 'cost']
				]
			}
		},
        
    ],
      styles: {
        header: {
          fontSize: 18,
          bold: true
        },
        subheader: {
          fontSize: 15,
          bold: true
        },
        quote: {
          italics: true
        },
        small: {
          fontSize: 8
        }
      }}
  var win = window.open('', '_blank');
  pdfMake.createPdf(docDefinition).open({}, win);
//this.PrintDetails=this.d+this.data+this.dataSource.data;
//alert(JSON.stringify(this.PrintDetails));
 }
}
