import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { LoginService } from '../../services/login.service';
import { BsModalService } from 'ngx-bootstrap/modal'; 
import { BsModalRef } from 'ngx-bootstrap/modal/bs-modal-ref.service';
import { ReactiveFormsModule } from '@angular/forms';
import { FormControl,Validators,FormGroupDirective} from '@angular/forms';
import { NgForm } from '@angular/forms';
import { Template } from '@angular/compiler/src/render3/r3_ast';
import { Router} from '@angular/router';
import {MatSnackBar,MatSnackBarConfig} from '@angular/material';
import {ToastrService} from 'ngx-toastr';
import { ok } from 'assert';
declare var $: any;
@Component({
  selector: 'app-login',

  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {
  isCollapsed = true;

  LoginDetails : any = {
    email : '',
    password:''
  };
//  email:any=[];
 
  showSpinner: boolean = false;
   
  ngOnInit() {
  }
  


 constructor(private router: Router,private loginService:LoginService,private snackBar:MatSnackBar,private toastr:ToastrService) { }
 
  login() {
     alert("welcome");
        this.loginService.getContacts(this.LoginDetails).subscribe(res => {
       if(res['status']==true)
       {
         if(res['role']=='patient')
         {
    this.snackBar.open('Login successfully','ok',{
       duration:5000,
 
    });
              localStorage.setItem('currentUser', JSON.stringify({ email:this.LoginDetails.email,password:this.LoginDetails.password,role:res['role']}));
              this.router.navigate(['/patient']);
       }  
       else 
       {  
       if(res['role']=='doctor')
       {
        localStorage.setItem('currentUser', JSON.stringify({ email:this.LoginDetails.email,password:this.LoginDetails.password,role:res['role']}));
        this.router.navigate(['/doctor']);
       }
      }}
      else
      {
        
   this.snackBar.open('Invalid Login', 'Ok', {
      duration: 5000,
        });
       // alert("invalid user");
         }

        })
        ;
      /** spinner starts on init */
      }
    };